import { useParams, useNavigate } from "react-router-dom"
import { toyService } from '../services/toy.service.js'
import { useState, useEffect } from "react"
import { Spinner } from '../cmps/spinner.js'
import Avatar from '@mui/material/Avatar';
import Rating from '@mui/material/Rating';

export const ToyDetails = () => {

    const [toy, setToy] = useState(null)
    const params = useParams()
    const navigate = useNavigate()

    useEffect(() => {
        loadToy()
    }, [])

    const loadToy = () => {
        const { toyId } = params
        toyService.getById(toyId)
            .then(toy => setToy(toy))
    }

    const goBack = () => {
        navigate(-1)
    }

    if (!toy) return <Spinner />
    return <section className="toy-details">
        <button className='btn-back' onClick={goBack}>Go Back</button>
        <div className='toy-details-container flex space-around'>
            <div className="toy-info-l">
                <img src={toy.imgSrc} alt="Toy image" />
            </div>
            <div className='toy-info-r flex col space-around'>
                <h1>Name: {toy.name}</h1>
                <p>SKU: {toy._id}</p>
                <span>Price: {toy.price}$</span>
                {toy.inStock ? <p style={{ color: 'green' }}>IN STOCK</p>
                    : <p style={{ color: 'red' }}>Out of stock</p>
                }
                <hr />
                {toy.labels &&
                    <div className="toy-categories">
                        <h3>Categories</h3>
                        <p>[{toy.labels.map((label, idx) => <span key={idx}>,{label}</span>)}]</p>
                    </div>
                }
            </div>
        </div>
        <div className='reviews'>
            <h3>Reviews:</h3>
            <div className='review-card flex space-between'>
                <div className="card-l flex col space-around">
                    <Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" />
                    <span>aaa</span>
                    <Rating name="read-only" value={5} readOnly />
                </div>
                <div className="card-r">
                    <h3>This Product is Amazing</h3>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquid facilis ab minus corrupti, omnis quia maxime vel voluptatem earum nesciunt dolores veritatis harum quod est adipisci repellendus dicta inventore voluptatibus!</p>
                </div>
            </div>
            <div className='review-card flex space-between'>
                <div className="card-l flex col space-around">
                    <Avatar alt="Travis Howard" src="/static/images/avatar/2.jpg" />
                    <span>Puki</span>
                    <Rating name="read-only" value={1} readOnly />
                </div>
                <div className="card-r">
                    <h3>recieved broken</h3>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquid facilis ab minus corrupti, omnis quia maxime vel voluptatem earum nesciunt dolores veritatis harum quod est adipisci repellendus dicta inventore voluptatibus!</p>
                </div>
            </div>
            <div className='review-card flex space-between'>
                <div className="card-l flex col space-around">
                    <Avatar alt="Cindy Baker" src="/static/images/avatar/3.jpg" />
                    <span>user user</span>
                    <Rating name="read-only" value={3} readOnly />
                </div>
                <div className="card-r">
                    <h3>This Product is Nice</h3>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquid facilis ab minus corrupti, omnis quia maxime vel voluptatem earum nesciunt dolores veritatis harum quod est adipisci repellendus dicta inventore voluptatibus!</p>
                </div>
            </div>

        </div>
    </section>

}