import { toyService } from "../../services/toy.service.js";

export function loadToys() {
    return (dispatch, getState) => {
        const { filter } = getState().toyModule
        toyService.query(filter)
            .then(toys => dispatch({ type: 'SET_TOYS', toys }))
    }
}
export function addToy(toy) {
    return (dispatch) => {
        toyService.save(toy)
            .then(toy => dispatch({ type: 'ADD_TOY', toy }))
    }
}
export function updateToy(toy) {
    return (dispatch) => {
        toyService.save(toy)
            .then(toy => dispatch({ type: 'UPDATE_TOY', toy }))
    }
}
export function removeToy(toyId) {
    return (dispatch) => {
        toyService.remove(toyId)
            .then(() => dispatch({ type: 'REMOVE_TOY', toyId }))
    }
}
export function setQueryFilter(filter) {
    return (dispatch) => {
        dispatch({ type: 'SET_FILTER', filter })
    }
}