import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Fab from '@mui/material/Fab';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
export const ToyPreview = ({ toy, handleRemove }) => {

    const [isHovering, setIsHovering] = useState(false)

    const handleMouseOver = () => {
        setIsHovering(true)
    }
    const handleMouseOut = () => {
        setIsHovering(false)
    }

    return <div className="toy-preview flex col align-center" onMouseOver={handleMouseOver} onMouseOut={handleMouseOut}>
        <Link to={`/toy/${toy._id}`}><img src={toy.imgSrc} alt="Toyimg" /></Link>
        <h1>{toy.name}</h1>
        <p>{toy.price}.00$</p>
        {isHovering &&
            <div className='preview-btns'>
                <Link to={`/edit/${toy._id}`}>
                <Fab size="small" color="primary" aria-label="add">
                    <EditIcon />
                </Fab>
                </Link>
                <Fab size="small" color="error" aria-label="add">
                    <DeleteIcon onClick={() => handleRemove(toy._id)} />
                </Fab>
            </div>
        }
    </div>
}