import { httpService } from './http.service.js'
import { utilService } from './util.service.js'

export const toyService = {
    getById,
    query,
    remove,
    save
}

const KEY = 'toy'

function getById(toyId) {
    return httpService.get(`${KEY}/${toyId}`)
}

function remove(toyId) {
    return httpService.delete(`${KEY}/${toyId}`)
}

function save(toy) {
    if (toy._id) return httpService.put(`${KEY}/${toy._id}`, toy)
    else return httpService.post(KEY, toy)
}

function query(filter) {

    return httpService.get(KEY)
        .then(toys => {
            if (!toys || !toys.length) {
                toys = _createToys()
                return httpService.postMany(KEY, toys)
            }
            if (filter) {
                let { name, inStock } = filter
                toys = toys.filter(toy => toy.name.includes(name))
                toys = toys.filter(toy => (inStock === true) ? toy.inStock : toy)
            }
            return toys
        })
}

function _createToy(price = utilService.getRandomIntInclusive(30, 150), labels) {
    return {
        name: utilService.makeLorem(1),
        price,
        labels,
        createdAt: Date.now(),
        inStock: true,
        imgSrc: `https://robohash.org/${utilService.makeLorem(1)}?set=set4`
    }
}

function _createToys() {
    console.log("Creating New Toys...")
    const labels = ["On wheels", "Box game", "Art", "Baby", "Doll", "Puzzle", "Outdoor"]
    const toys = []
    for (let i = 0; i < 20; i++) {
        const categories = []
        for (let j = 0; j < 3; j++) {
            const label = labels[utilService.getRandomIntInclusive(0, labels.length - 1)]
            categories.push(label)
        }
        toys.push(_createToy(undefined, categories))
    }
    return toys
}
