import { useEffect, useState } from "react"
import { useDispatch } from "react-redux"
import { utilService } from "../services/util.service.js"
import { useParams, useNavigate } from "react-router-dom"
import { addToy, updateToy } from '../store/actions/toys.action.js'
import { toyService } from "../services/toy.service.js"
import { TextField, Button } from '@mui/material'
import SendIcon from '@mui/icons-material/Send';

export const ToyEdit = () => {
    const dispatch = useDispatch()
    const params = useParams()
    const navigate = useNavigate()

    const [toy, setToy] = useState({
        name: '',
        price: 0,
        inStock: true,
        labels: [],
        imgSrc: `https://robohash.org/${utilService.makeLorem(1)}?set=set4`
    })

    useEffect(() => {
        const { toyId } = params
        if (toyId) {
            toyService.getById(toyId)
                .then(toy => setToy(toy))
        }
    }, [])

    const handleChange = ({ target }) => {
        const name = target.name
        let value
        switch (target.type) {
            case 'number':
                value = +target.value
                break;
            case 'select-one':
                value = (target.value === 'true') ? true : false
                break
            default:
                value = target.value
        }
        setToy(prevToy => ({ ...prevToy, [name]: value }))
    }

    const handleSave = (ev) => {
        ev.preventDefault()
        if (toy._id) {
            dispatch(updateToy(toy))
            navigate(-1)
            return
        }
        dispatch(addToy(toy))
        navigate(-1)
    }


    return <section className="toy-edit">
        <h1>Toy Edit</h1>
        <form className="flex col space-between" onSubmit={handleSave}>
            <div className='form-item'>
                <TextField id="standard-textarea" label="Toy Name" value={toy.name} name="name" onChange={handleChange} multiline variant="standard" />
            </div>
            <div className='form-item'>
                <TextField id="standard-textarea" label="Toy Price" value={toy.price} name="price" onChange={handleChange} multiline variant="standard" />
            </div>
            <div className='form-item'>
                <label htmlFor="inStock">Stock:</label>
                <select name="inStock" id="inStock" value={toy.inStock} onChange={handleChange}>
                    <option value="true">In stock</option>
                    <option value="false">Out for stock</option>
                </select>  
            </div>
            <Button type="submit" variant="contained" endIcon={<SendIcon />}>
                Save
            </Button>
        </form>
    </section>
}