import { useState } from "react";
import GoogleMapReact from 'google-map-react';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';

const AnyReactComponent = ({ text }) => <div>{text}</div>;

export function Map() {

    const [coordinates, setCoordinates] = useState({ lat: 32.0853, lng: 34.7818 })
    const zoom = 12
    const branchesMarkers = [
        { city: 'Tel Aviv', position: { lat: 32.0853, lng: 34.7818 } },
        { city: 'Bat Yam', position: { lat: 32.016499, lng: 34.750278 } },
        { city: 'Ashkelon', position: { lat: 40.712776, lng: -74.005974 } },
    ]

    const setMapPos = ({ lat, lng }) => {
        setCoordinates({ lat, lng })
    }

    return (
        <div className="map-container">
            <div className="map">
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyA36b7We_tjp91ABvxos4yefbmxkgPNUIM" }}
                    center={coordinates}
                    zoom={zoom}>
                    {branchesMarkers.map(({ position: { lat, lng } }) => <AnyReactComponent key={lat} lat={lat} lng={lng} text={<LocationOnIcon />} />)}
                </GoogleMapReact>
            </div>
            <ButtonGroup variant="outlined" color="success" aria-label="outlined button group">
                <Button onClick={() => setMapPos({lat: 32.0853, lng: 34.7818 })}>Tel Aviv</Button>
                <Button onClick={() => setMapPos({lat: 32.016499, lng: 34.750278 })}>Bat Yam</Button>
                <Button onClick={() => setMapPos({lat: 40.712776, lng: -74.005974 })}>New York</Button>
            </ButtonGroup>
        </div>
    )
}