import { useEffect } from 'react'
import { useSelector, useDispatch } from "react-redux"
import { loadToys, removeToy, setQueryFilter } from '../store/actions/toys.action.js'
import { ToyList } from '../cmps/toy-list.jsx'
import { ToyFilter } from '../cmps/toy-filter.jsx'
import { Link } from 'react-router-dom'
import { Spinner } from '../cmps/spinner.js'
import AddIcon from '@mui/icons-material/Add';
import Fab from '@mui/material/Fab';

export const ToyApp = () => {


    const { toys } = useSelector(state => state.toyModule)
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(loadToys())
    }, [])

    const handleRemove = (toyId) => {
        dispatch(removeToy(toyId))
    }

    const setToysFilter = (filter) => {
        dispatch(setQueryFilter(filter))
        dispatch(loadToys())
    }

    if (!toys) return <Spinner />

    return <section className='toy-app'>
        <Link to='/new'>
            <Fab color="secondary" aria-label="add">
                <AddIcon />
            </Fab>
        </Link>
        {/* <Link to='/new'><button className='btn-new-toy'>Add New Toy</button></Link> */}
        <ToyFilter setToysFilter={setToysFilter} />
        <ToyList toys={toys} handleRemove={handleRemove} />
    </section>
}